<template>
  <div :class="$root.themeHomeChange === '1' ? 'planEntry_light' : 'planEntry'">
    <div class="planEntry-con">
      <div class="headBox">
        <span class="title"> 监督计划录入 </span>
        <bda-button @click="goBack">返回</bda-button>
      </div>
      <div class="content">
        <el-form
          :model="form"
          :rules="rules"
          ref="form"
          label-width="150px"
          label-suffix="："
          label-position="left"
          v-loading="formLoading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
        >
          <el-row :gutter="50">
            <el-col :span="12">
              <bda-form-item label="监督类型" prop="type">
                <bda-select
                  v-model="form.type"
                  size="small"
                  filterable
                  clearable
                  placeholder="请选择"
                  style="width: 100%"
                >
                  <bda-option
                    v-for="(item, index) in options['type']"
                    :label="item.label"
                    :value="item.value"
                    :key="index"
                  ></bda-option>
                </bda-select>
              </bda-form-item>
            </el-col>
            <el-col :span="12">
              <bda-form-item label="监督领域" prop="jdly">
                <bda-select
                  v-model="form.jdly"
                  size="small"
                  filterable
                  clearable
                  placeholder="请选择"
                  style="width: 100%"
                >
                  <bda-option
                    v-for="(item, index) in options['jdly']"
                    :label="item.label"
                    :value="item.value"
                    :key="index"
                  ></bda-option>
                </bda-select>
              </bda-form-item>
            </el-col>
            <el-col :span="12">
              <bda-form-item label="单位名称" prop="dwmc">
                <SelectTree
                  style="width: 100%"
                  ref="selectTreeRef"
                  :props="defaultProps"
                  :options="treeData"
                  :value="valueId"
                  :label="form.dwmc"
                  :clearable="true"
                  :accordion="true"
                  @getValue="getValue($event)"
                />
              </bda-form-item>
            </el-col>
            <el-col :span="12">
              <bda-form-item label="配合部门" prop="phbm">
                <bda-select
                  v-model="form.phbm"
                  filterable
                  clearable
                  :searchable="false"
                  placeholder="请选择"
                  style="width: 100%"
                  size="small"
                >
                  <bda-option
                    v-for="(item, index) in options['phbm']"
                    :label="item.departmentName"
                    :value="item.departmentCode"
                    :key="index"
                  ></bda-option>
                </bda-select>
              </bda-form-item>
            </el-col>
            <el-col :span="12">
              <bda-form-item label="年度" prop="nd">
                <el-date-picker
                  v-model="form.nd"
                  type="year"
                  placeholder="请选择年度"
                  style="margin-right: 15px;"
                  value-format="yyyy"
                >
                </el-date-picker>
              </bda-form-item>
            </el-col>
            <el-col :span="12">
              <bda-form-item label="监督项目名称" prop="jdxmmc">
                <el-input
                  placeholder="请输入监督项目名称"
                  v-model="form.jdxmmc"
                  clearable
                  size="small">
                </el-input>
              </bda-form-item>
            </el-col>
            <el-col :span="12">
              <bda-form-item label="检查对象" prop="jcdx">
                <el-input
                  placeholder="请输入检查对象"
                  v-model="form.jcdx"
                  clearable
                  size="small">
                </el-input>
              </bda-form-item>
            </el-col>
            <el-col :span="12">
              <bda-form-item label="计划开展时间 (月份)" prop="planDate">
                <div style="display: flex;">
                  <el-input-number
                    v-model="form.jhsjBegin"
                    style="width: 45%; margin: 0 auto;"
                    :min="1"
                    :max="12"
                    placeholder="开始时间"
                  ></el-input-number>
                  <el-input-number
                    v-model="form.jhsjEnd"
                    style="width: 45%; margin: 0 auto;"
                    :min="1"
                    :max="12"
                    placeholder="结束时间"
                  ></el-input-number>
                </div>
              </bda-form-item>
            </el-col>
            <el-col :span="24" style="height: 100px;">
              <bda-form-item label="监督内容" prop="jdnr">
                <el-input
                  resize="none"
                  type="textarea"
                  :autosize="{ minRows: 3, maxRows: 3}"
                  placeholder="请输入监督内容"
                  v-model="form.jdnr">
                </el-input>
              </bda-form-item>
            </el-col>
            <el-col :span="12">
              <bda-form-item label="开展方式" prop="kzfs">
                <el-input
                  placeholder="请输入开展方式"
                  v-model="form.kzfs"
                  clearable
                  size="small">
                </el-input>
              </bda-form-item>
            </el-col>
            <el-col :span="12">
              <bda-form-item label="完成标志" prop="wcbz">
                <el-input
                  placeholder="请输入完成标志"
                  v-model="form.wcbz"
                  clearable
                  size="small">
                </el-input>
              </bda-form-item>
            </el-col>
            <el-col :span="24">
              <bda-form-item label="备注" prop="remark">
                <el-input
                  resize="none"
                  type="textarea"
                  :autosize="{ minRows: 1, maxRows: 2}"
                  placeholder="请输入备注"
                  v-model="form.remark">
                </el-input>
              </bda-form-item>
            </el-col>
          </el-row>
        </el-form>
        <div class="submit" >
          <el-button @click="submit()" type="primary"> 提交 </el-button>
          <el-button @click="goBack()"> 取消 </el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from "@/base/mixin/risk-control-center.js";
import SelectTree from "@/views/components/orgTreeSelect";
// import qs from "qs";
// import moment from "moment";

export default {
  components: { SelectTree },
  mixins: [Interface],
  props: {
    formData_in: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    var check_planDate = (rule, value, callback) => {
      if (this.form.jhsjBegin && this.form.jhsjEnd) {
        if(this.form.jhsjBegin > this.form.jhsjEnd) {
          return callback(new Error('计划开始时间不得大于计划结束时间'));
        } else {
          return callback();
        }
      } else {
        return callback(new Error('请填写完整计划开展时间'));
      }
    };
    return {
      // 单位选择 变量
      defaultProps: {
        children: "list",
        label: "orgName",
        value: "orgId",
      },
      treeData: [],
      valueId: '',
      
      //form
      form: {
        dwmc: ''
      },
      
      detailInfo: [], // 详细信息
      
      keyValue: '', //关键字
      
      chartData: {}, // 图表数据
      
      infoLoading: false,  // 基本信息加载
      
      infoData: {}, // 基本信息
      
      // 表单加载
      formLoading: false,
      
      // 风险监测情况加载
      information_Loading: false,
      
      /*表格数据 */
      tableData: [],

      pageInfoConfig: {
        page: 1,
        rows: 20,
        total: 0,
      }, //表格-分页初始配置
      
      options: {
        type: [
          {
            label: '职能监督',
            value: '职能监督'
          },
          {
            label: '专业监督',
            value: '专业监督'
          },
          {
            label: '专责监督',
            value: '专责监督'
          },
          {
            label: '安全监督',
            value: '安全监督'
          },
          {
            label: '基层单位日常监督',
            value: '基层单位日常监督'
          }
        ],
        phbm: [],
        jdly: [
          {
            label: '安全监管领域',
            value: '安全监管领域'
          },
          {
            label: '市场营销领域',
            value: '市场营销领域'
          },
          {
            label: '工程建设领域',
            value: '工程建设领域'
          },
          {
            label: '大党建办公综合领域',
            value: '大党建办公综合领域'
          },
          {
            label: '财务报销领域',
            value: '财务报销领域'
          },
          {
            label: '创新与数字化领域',
            value: '创新与数字化领域'
          },
          {
            label: '其他',
            value: '其他'
          }
        ],
        gdfj: [
          {
            label: '古镇供电分局',
            value: '古镇供电分局'
          },
          {
            label: '三溪供电分局',
            value: '三溪供电分局'
          },
          {
            label: '大涌供电分局',
            value: '大涌供电分局'
          },
          {
            label: '石碑供电分局',
            value: '石碑供电分局'
          }
        ],
      },
      
      rules: {
        type: [
          { required: true, message: '请选择监督类型', trigger: 'change' }
        ],
        dwmc: [
          { required: true, message: '请选择单位名称', trigger: 'change' }
        ],
        jdly: [
          { required: false, message: '请选择监督领域', trigger: 'change' }
        ],
        nd: [
          { required: true, message: '请选择年度', trigger: 'change' }
        ],
        qtbm: [
          { required: true, message: '请选择牵头部门', trigger: 'change' }
        ],
        phbm: [
          { required: false, message: '请选择配合部门', trigger: 'change' }
        ],
        jcdx: [
          { required: true, message: '请输入检查对象', trigger: 'blur' },
        ],
        planDate: [
          { required: true, validator: check_planDate, trigger: 'blur' }
        ],
        jdxmmc: [
          { required: true, message: '请输入监督项目名称', trigger: 'blur' },
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        jdnr: [
          { required: true, message: '请输入监督内容', trigger: 'blur' },
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        kzfs: [
          { required: false, message: '请输入开展方式', trigger: 'blur' },
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        wcbz: [
          { required: false, message: '请输入完成标志', trigger: 'blur' },
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        remark: [
          { required: false, message: '请输入备注', trigger: 'blur' },
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
      },
    };
  },
  created() {
    this.init();
  },
  mounted() {},
  watch: {
    formData_in: {
      handler(v) {
        // window.console.log(v);
        this.form = JSON.parse(JSON.stringify(v)) || { dwmc: '' };
        this.valueId = v.dwbm || '';
      },
      immediate: true,
      deep: true
    },
  },
  methods: {
    init() {
      this.getOrgData();
      this.getPhbmData();
    },
    // 获取单位树
    getOrgData() {
      this.getSupervisionUnitTree().then((rs)=>{
        // window.console.log(rs)
        if(Array.isArray(rs) && rs.length !== 0) {
          // window.console.log(rs)
          this.treeData = rs;
        } else {
          this.treeData = [];
        }
      }).finally(()=>{});
    },
    // 获取配合部门
    getPhbmData() {
      this.supervisionCoordinate().then((rs)=>{
        // window.console.log(rs)
        if(Array.isArray(rs) && rs.length !== 0) {
          // window.console.log(rs)
          this.options['phbm'] = rs;
        } else {
          this.options['phbm'] = [];
        }
      }).finally(()=>{});
    },
    // 取值
    getValue(item){
      // window.console.log(item)
      this.form.dwbm = item[0];
      this.form.dwmc = item[1];
    },
    
    // 提交表单
    submit() {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.$confirm(`此操作将 ${ this.form.id ? '编辑' : '录入'} 当前监督计划, 是否继续?`, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.formLoading = true;
            // window.console.log('监督计划录入表单', this.form);
            this[this.form.id ? 'supervisionUpdate' : 'supervisionInsert'](this.form).then(()=>{
              this.$message.success('提交成功');
              this.goBack();
            }).finally(()=>{
              this.formLoading = false;
            });
          }).catch(() => {});
        } else {
          return false;
        }
      });
    },
    // 返回页面
    goBack() {
      this.$emit('closeDilag');
    }
  },
};
</script>

<style lang="scss" scoped>
.planEntry {
  width: 100%;
  height: calc(100%);
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  background-color: rgba($color: #333, $alpha: .5);
  z-index: 999;
  .planEntry-con {
    min-width: 800px;
    max-width: 1200px;
    min-height: 690px;
    max-height: 800px;
    width: 70%;
    border-radius: 10px;
    // position: relative;
    // margin: 100px auto 50px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 0 10px;
    border-left: 1px solid #4389f9;
    border-right: 1px solid #4389f9;
    border-bottom: 1px solid #4389f9;
    background: rgba($color: #041338, $alpha: .9);
    .headBox {
      width: calc(100%);
      height: 5%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px;
      margin-left: -10px;
      background-color: rgba($color: #4389f9, $alpha: .2);
      border-radius: 10px 10px 0 0;
      // border-bottom: 1px solid #ccc;
      .title {
        font-size: 16px;
        font-weight: bold;
        color: #eee;
        display: block;
        padding: 5px 10px;
        border-left: 4px solid #4389f9;
      }
    }
    .content {
      width: calc(100% - 30px);
      height: calc(95% - 41px);
      padding: 10px 25px 50px;
      // margin-top: 10px;
      margin-left: -10px;
      border-top: 1px solid #4389f9;
      position: relative;
      .submit {
        position: absolute;
        right: 15px;
        bottom: 15px;
        text-align: right;
      }
      /deep/ {
        .el-input-number__decrease, .el-input-number__increase {
          background-color: #041338;
          border-color: #4298F3;
          color: #fff;
        }
        // 按钮样式
        .el-button--default {
          background-color: #041338;
          color: #4298F3;
          border: 1px solid #2a5cb0;
          border-radius: 3px;
          padding: 8px 25px;
        }
        .el-button--primary {
          background-color: #4389F9;
          color: #ffffff;
          border-radius: 3px;
          text-align: center;
          transition: all .3s;
          padding: 8px 25px;
          &:hover {
            color: #4298F3;
            background-color: #041338;
            border: 1px solid #2a5cb0;
          }
        }
        .el-form {
          margin-bottom: 10px;
          .el-row {
            // height: 32px;
            .el-col {
              height: 62px;
              margin-bottom: 13px;
              padding-top: 32px;
              position: relative;
              .el-form-item__label {
                position: absolute;
                top: 0;
                left: 25px;
                color: #fff;
              }
              .el-form-item__content {
                margin-left: 0!important;
                .bda-date-picker {
                  width: 100%;
                }
              }
              .el-input__inner {
                background: #041338;
                border: 1px solid #4389f9;
                border-radius: 4px;
                color: #4298f3;
              }
              
              .el-input__inner, .el-textarea__inner, .el-upload--picture-card{
                background-color: #041338;
                color: #4298F3;
                border: 1px solid #2a5cb0;
                border-radius: 3px;
                text-align: left;
                &::placeholder {
                color: #4298f3 !important;
              }
              }
        
              .el-date-editor {
                width: 100%;
        
                .el-range-input {
                  background-color: #041338;
                  color: #4298f3;
                  &::placeholder {
                    color: #4298f3;
                  }
                }
                .el-range-separator {
                  color: #fff;
                }
        
                .el-input__icon,
                .el-input__suffix {
                  background: rgba($color: #041338, $alpha: 0) !important;
                }
              }
            }
          }
        }
      }
    }
  }
}

// 浅色主题
